import { GraphNode } from "./graph";
export class Clip {
    constructor(name) {
        this.length = 1;
        this.extent = 1;
        this.extentPast = 0;
        this.start = 0;
        this.name = name;
    }
    get totalExtent() {
        return this.extent - this.extentPast;
    }
}
export class AbstractClip extends Clip {
    constructor(name = "") {
        super(name);
    }
    duplicate() {
        const clip = new AbstractClip();
        clip.length = this.length;
        clip.extent = this.extent;
        clip.extentPast = this.extentPast;
        clip.start = this.start;
        return clip;
    }
}
export class Track extends GraphNode {
    constructor() {
        super(...arguments);
        this.name = "Track name";
        this.enabled = true;
        this.height = 64;
        this.clips = [];
    }
}
