import { ProcessingGraph } from "@api/graph";
export class Project {
    constructor() {
        this.name = "New Project";
        this.sampleRate = 48000;
        this.tempo = 136;
        this.signature = [4, 4];
        this.tracks = [];
        this.graph = new ProcessingGraph();
    }
    timeToBeats(timeInSeconds) {
        return timeInSeconds / 60 * this.tempo;
    }
    beatsToTime(beats) {
        return beats / this.tempo * 60;
    }
    timeToBars(timeInSeconds) {
        return this.timeToBeats(timeInSeconds) / this.signature[0];
    }
    barsToTime(bars) {
        return this.beatsToTime(bars * this.signature[0]);
    }
    get quantum() {
        return 128 / this.sampleRate;
    }
}
